import React from 'react'



function RowData({toDos,setTodos,passComponent,dragItem,dragOverItem,currentStatus ,currentSecStatus,colour}) {
    
  return (
    <div id={String(currentStatus)} className={`col-md-3 ${colour} px-0 m-1`} style={{minHeight: '200px'}}
    onDragEnd={(e)=>{
      
      setTodos(toDos.filter((value,index)=>{
        if(index===dragItem.current){
          let date=new Date().toLocaleDateString();
          value.status=String(dragOverItem.current)
          value.date=date

        }
        return value;

      }))

    }}
    onDragOver={(e)=>{
      
      dragOverItem.current=String(currentStatus)
      e.preventDefault();
      
     

    }}>
                <div>
                  <h4 className="ms-2">{currentStatus}</h4>
                </div>
                <div className="mt-2">

                {
                  toDos.map((obj,index)=>{
                    if(obj.status===String(currentStatus)||obj.status===String(currentSecStatus)){
                   
                  return(
                    passComponent(obj,index)
                  )}
                  else{
                    return(null)
                  }
                  })
                }     



                </div>
              </div>
  )
}

export default RowData