import React,{ useState,useRef } from "react";
import CompleteList from "./CompleteList";
import DataList from "./DataList";
import Header from "./Header";
import RowData from "./RowData";



function App() {
const [toDos,setTodos]=useState([]);
const [toDo,setTodo]=useState('');
let dragItem=useRef();
let dragOverItem=useRef()


  return (
        <section className="container-fluid">
            <Header toDos={toDos} setTodos={setTodos} toDo={toDo} setTodo={setTodo} />
            <div className="row mt-4 mx-3 justify-content-center">
              <RowData toDos={toDos} setTodos={setTodos} passComponent={(obj,index)=><DataList obj={obj} key={index} index={index} dragItem={dragItem} toDos={toDos} setTodos={setTodos} />} dragItem={dragItem} dragOverItem={dragOverItem} currentStatus="Todo"  colour="bg-info" />
              <RowData toDos={toDos} setTodos={setTodos}  passComponent={(obj,index)=><DataList obj={obj} key={index} index={index} dragItem={dragItem}  toDos={toDos} setTodos={setTodos} />} dragItem={dragItem} dragOverItem={dragOverItem} currentStatus="Progress" colour="bg-warning"/>
              <RowData toDos={toDos} setTodos={setTodos} passComponent={(obj,index)=><CompleteList obj={obj} key={index} toDos={toDos} setTodos={setTodos}  />} dragItem={dragItem} dragOverItem={dragOverItem} currentStatus="Completed" currentSecStatus="Cancelled" colour="bg-success" />
            </div>
            
          </section>
        
      
    
  );
}

export default App;
